import React from 'react';
import Activity from "./Activity";
import {activityData2018, activityData2019, activityData2020} from "../../data/activity_data";
import Label from "./Label";
import "./timeline.scss";

const Timeline = () => {
    return (
        <div className="timeline">
            <Label year={"2020"}/>
            {activityData2020.map((item, i) => {
                return <Activity key={i} href={item.href} title={item.title} type={item.type} date={item.date}
                                 location={item.location} img={item.img} text={item.text}/>
            })}
            <Label year={"2019"}/>
            {activityData2019.map((item, i) => {
                return <Activity key={i} href={item.href} title={item.title} type={item.type} date={item.date}
                                 location={item.location} img={item.img} text={item.text}/>
            })}
            <Label year={"2018"}/>
            {activityData2018.map((item, i) => {
                return <Activity key={i} href={item.href} title={item.title} type={item.type} date={item.date}
                                 location={item.location} img={item.img} text={item.text}/>
            })}
        </div>
    )
};

export default Timeline;
