import React from 'react';
import Badge from "./Badge";
import {Link} from "react-router-dom";

const Activity = ({href, title, type, date, location, img, text}) => {

    const generateImage = (img) => {
        if (img) {
            return <img src={img} alt="cover" className="card-img"/>;
        }
    };

    const checkClickable = (href) => href ? " clickable" : " non-clickable";

    return (
        <div className="timeline-item">
            <Badge type={type}/>
            <Link className={"card timeline-panel" + checkClickable(href)} to={href ? href : "#"}>
                {generateImage(img)}
                <div className="card-body">
                    <div className="panel-header">
                        <h5>{title}</h5>
                        <p className="badge badge-location">@{location}</p>
                    </div>
                    <p className="text-muted">{date}</p>
                    <p>{text}</p>
                </div>
            </Link>
        </div>
    )
};

export default Activity;
