import React from 'react';
import Timeline from "./timeline/Timeline";
import {Redirect, Route, Switch} from "react-router";
import Profile from "./profile/Profile";
import Gallery from "./gallery/Gallery";
import Reflection from "./reflection/Reflection";
import Selection from "./selection/Selection";

const Content = () => {
    return (
        <div className="container">
            <Switch>
                <Route exact path="/" component={RedirectToProfile}/>
                <Route exact path="/profile" component={Profile}/>
                <Route exact path="/activities" component={Timeline}/>
                <Route exact path="/activities/:id" component={Selection}/>
                <Route exact path="/gallery" component={Gallery}/>
                <Route exact path="/reflection" component={Reflection}/>
            </Switch>
        </div>
    )
};

const RedirectToProfile = () => <Redirect to={"/profile"}/>;

export default Content;
