import React from 'react';
import {Innovation, International, Seminar, Personal, Engagement} from "../../data/activity_data";

const Badge = ({type}) => {
    if (type === Innovation) {
        return (
            <div className="timeline-badge-innovation">
                <i className="fas fa-flask"/>
            </div>
        )
    } else if (type === International) {
        return (
            <div className="timeline-badge-international">
                <i className="fa fa-plane"/>
            </div>
        )
    } else if (type === Seminar) {
        return (
            <div className="timeline-badge-seminar">
                <i className="fa fa-microphone"/>
            </div>
        )
    } else if (type === Personal) {
        return (
            <div className="timeline-badge-personal">
                <i className="fa fa-user"/>
            </div>
        )
    } else if (type === Engagement) {
        return (
            <div className="timeline-badge-engagement">
                <i className="fa fa-walking"/>
            </div>
        )
    }
};

export default Badge;
