import React from 'react';
import {NavLink} from "react-router-dom";

const Header = () => {
    return (
        <nav className="navbar navbar-expand-lg navbar-light sticky-top bg-light">
            <div className="navbar-brand">
                <img src="/images/jensbnt.png" width="30" height="30"
                     className="d-inline-block align-top" alt=""/>
            </div>
            <button className="navbar-toggler" type="button" data-toggle="collapse"
                    data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false"
                    aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"/>
            </button>
            <div className="collapse navbar-collapse" id="navbarNav">
                <ul className="navbar-nav">
                    <li className="nav-item">
                        <NavLink className="nav-link" to={"/profile"}>Profiel</NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink className="nav-link" to={"/activities"}>Tijdlijn</NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink className="nav-link" to={"/gallery"}>Galerij</NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink className="nav-link" to={"/reflection"}>Reflectie</NavLink>
                    </li>
                </ul>
            </div>
        </nav>
    )
};

export default Header;
