import React from 'react';
import VPW from "./VPW";
import Baelor from "./Baelor";
import Germany from "./Germany";
import Careathon from "./Careathon";

class Selection extends React.Component {
    state = {
        id: undefined
    };

    componentDidMount() {
        const {id} = this.props.match.params;
        this.setState({id: Number(id)});
    }

    render() {
        const {id} = this.state;
        if (id === 1) {
            return (
                <VPW/>
            )
        } else if (id === 2) {
            return (
                <Baelor/>
            )
        } else if (id === 3) {
            return (
                <Germany/>
            )
        } else if (id === 4) {
            return (
                <Careathon/>
            )
        } else {
            return (
                <>Wrong ID</>
            )
        }
    }
}

export default Selection;
